export default {
	props: {
		item: Object,//父组件传来的每一个对象
		index: Number,//对象索引
	},
	data(){
		return{
			isFocus:this.item.isFocus,
			infonum:this.item.infonum
		}
	},
	onLoad() {

	},
	methods: {
		//点击关注
		onFocus(){
			this.isFocus = true;
			uni.showToast({
				title:"关注成功",
			})
		},
		//顶和踩
		onHandle(type){
			switch(type){
				case 'up':
				if(this.infonum.index==1){ return;};
				this.infonum.upnum++;
				if(this.infonum.index==2){
					this.infonum.downnum--;
				}
				this.infonum.index=1;
					break;
				case 'down':
				if(this.infonum.index==2){ return;};
				this.infonum.downnum++;
				if(this.infonum.index==1){
					this.infonum.upnum--;
				}
				this.infonum.index=2;
					break;
			}
		},
		//跳转详情页
		openDetail(){
			console.log("详情页")
		}
	}
}
